import React, {Component} from 'react';
import {GetQueryString} from '../../util/Util.js';
import {network} from '../../core/config.js'
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import * as Actions from '../../redux/actions.js';
import './Buy.css'
class Buy extends Component {
	constructor() {
		super();
		this.state = {
			account: {}
		}
		document.title = '购买账户';
	}
	componentDidMount() {
		let scatter = this.props.scatter;
		let eos = this.props.eos;
		let _this = this;
		if (scatter && eos) {
			scatter.getIdentity({accounts: [network]}).then((identity) => {
				let account = identity.accounts[0];
				let options = {
					authorization: account.name + '@' + account.authority, broadcast: true, sign: false
				};
				eos.getAccount(account.name).then(data => {
					_this.setState({
						account: {
							name: data.account_name, public_key: data.permissions[0].required_auth.keys[0].key
							//permissions[0].required_auth.keys[0].key
						}
					})
				})
			}).catch((e) => {
				if (e.code == 423) {
					alert('Scatter is locked, please unlock it so that you can login with Scatter')
				}
			})
		}
	}
	componentWillReceiveProps(nextProps){
		let scatter = nextProps.scatter;
		let eos = nextProps.eos;
		let _this = this;
		if (scatter && eos) {
			scatter.getIdentity({accounts: [network]}).then((identity) => {
				let account = identity.accounts[0];
				let options = {
					authorization: account.name + '@' + account.authority, broadcast: true, sign: false
				};
				eos.getAccount(account.name).then(data => {
					_this.setState({
						account: {
							name: data.account_name,
							public_key: data.permissions[0].required_auth.keys[0].key
							//permissions[0].required_auth.keys[0].key
						}
					})
				})
			}).catch((e) => {
				if (e.code == 423) {
					alert('Scatter is locked, please unlock it so that you can login with Scatter')
				}
			})
		}
	}
	buy = ()=>{
		let scatter = this.props.scatter;
		let eos = this.props.eos;
		let _this = this;
		if (scatter && eos) {
			scatter.getIdentity({accounts: [network]}).then((identity) => {
				let account = identity.accounts[0];
				let options = {
					authorization: account.name + '@' + account.authority, broadcast: true, sign: false
				};
				eos.getAccount(account.name).then(data => {
					console.log([
						{
							account: 'eosio.token',
							name: 'transfer',
							authorization: [{
								actor: account.name,
								permission: account.authority
							}],
							data: {
								from: account.name,
								to: 'eosnamedapp1',
								quantity: _this.state.price+' EOS',
								memo: '1-'+account.name+'-'+data.permissions[0].required_auth.keys[0].key
							}
						}
					])
					eos.transaction({
						actions: [
							{
								account: 'eosio.token',
								name: 'transfer',
								authorization: [{
									actor: account.name,
									permission: account.authority
								}],
								data: {
									from: account.name,
									to: 'eosnamedapp1',
									quantity: GetQueryString('price')+' EOS',
									memo: '1-'+account.name+'-'+data.permissions[0].required_auth.keys[0].key
								}
							}
						]
					}).then((data)=>{
						alert('帐号购买成功，可登录EOS浏览器查看');
						window.location.href = window.location.origin+'/exchange'
					}).catch(e=>console.log(e))
				})

			}).catch((e) => {
				if (e.code == 423) {
					alert('Scatter is locked, please unlock it so that you can login with Scatter')
				}
			})
		}

	};
	render() {
		return (
			<div className="buy-super-container">
				<div className="buy-container col-lg-6 col-xs-12">
					<div className="buy-account-container">
						<div className="buy-subline">
							<div className="buy-subline-name">出售账户</div>
							<div className="buy-name">{GetQueryString('account')}</div>
						</div>
						<div className="buy-subline">
							<div className="buy-subline-name">售价</div>
							<div className="buy-subline-value">{GetQueryString('price')}EOS</div>
						</div>
						<div className="buy-subline">
							<div className="buy-subline-name">收款账户</div>
							<div className="buy-subline-value">{GetQueryString('username')}</div>
						</div>
					</div>
					<div className="buy-account-container">
						<div className="buy-subline">
							<div className="buy-subline-name">购买账户</div>
							<div className="buy-name">{this.state.account.name}</div>
						</div>
					</div>
					<div className="buy-price">
						<div className="buy-tip">购买金额</div>
						<div className="buy-price-container">
							<div style={{fontSize: '1.6rem'}}>{GetQueryString('price')}</div>
							<div className="buy-tip">EOS</div>
						</div>
					</div>
					<div className="buy-price">
						<div className="buy-tip">过户公钥</div>
						<p style={{
							marginLeft: '-2.4rem',
							fontSize: '10px',
							transform: 'scale(0.8)',
							WebkitTransform: 'scale(0.8)'
						}}><span >{this.state.account.public_key}</span></p>
					</div>
					<p style={{fontSize: '1rem', fontWeight: 300}}>购买账户将过户到此公钥，默认为当前账户Active权限公钥</p>
					<button onClick={this.buy} className="btn btn-primary btn-lg btn-block">确定下单</button>
				</div>
			</div>
		)
	}
}
function mapStateToProps(state) {
	return {
		scatter: state.scatter.scatter, eos: state.eos.eos
	}
}

function mapDispatchToProps(dispatch) {
	return {
		actions: bindActionCreators(Actions, dispatch)
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Buy)


