import React, {Component} from 'react';
import {Link} from "react-router-dom";
import './NavBottom.css';
export default class NavBottom extends Component {
	render() {
		return (
			<div className="nav-bottom" style={{display:this.props.display?'flex':'none'}}>
				<Link to="/exchange/list"
				      className={this.props.active === 1 ? "nav-bottom-item nav-bottom-active" : 'nav-bottom-item'}>
					<i style={{fontSize: '2.4rem'}} className="iconfont">&#xe614;</i>
					<p >交易</p>
				</Link>
				<Link to='/main'
				      className={this.props.active === 2 ? "nav-bottom-item nav-bottom-active" : 'nav-bottom-item'}>
					<i style={{fontSize: '2.4rem'}} className="iconfont">&#xe60f;</i>
					<p >我的</p>
				</Link>
			</div>
		)
	}
}


