import React, {Component} from 'react';
import {Route, Link, Switch, Redirect} from "react-router-dom";
import Sale from '../sale/Sale.js';
import ExchangeList from '../exchangelist/ExchangeList';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import * as Actions from '../../redux/actions.js';
import './Exchange.css'
import NavBottom from '../../component/navBottom/NavBottom.js';
class Exchange extends Component {
	constructor(match) {
		super(match);
		this.state = {
			active: 1
		}
	}

	render() {
		return (
			<div>
				<div style={{paddingTop: '6rem'}}>
					<ul className="nav nav-pills exchange-sub-nav navbar-fixed-top" style={{background: '#fff',display:!this.props.isPC?'flex':'none'}}>
						<li
							role="presentation"
							onClick={() => this.setState({active: 1})}
							className={window.location.href.indexOf('/exchange/list') > 0 ? 'active' : ''}>
							<Link to="/exchange/list">购买</Link>
						</li>
						<li
							role="presentation"
							onClick={() => this.setState({active: 2})}
							className={window.location.href.indexOf('/sale') > 0 ? 'active' : ''}>
							<Link to={`${this.props.match.path}/sale`}>出售</Link>
						</li>
					</ul>
					<Switch>
						<Route path={`${this.props.match.path}/sale`} component={Sale}/>
						<Route path={`${this.props.match.path}/list`} component={ExchangeList}/>
						<Redirect to={`${this.props.match.path}/list`}/>
					</Switch>
					<NavBottom display={!this.props.isPC} active={1}/>
				</div>
			</div>
		);
	}
}
function mapStateToProps(state) {
	return {
		isPC:state.isPC.isPC
	}
}

function mapDispatchToProps(dispatch) {
	return {
		actions: bindActionCreators(Actions, dispatch)
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Exchange)




