export const GETSCATTER = 'GETSCATTER';
export const GETEOS = 'GETEOS';
export const SETEXCHANGELIST = 'SETEXCHANGELIST';
export const ISPC = 'ISPC';

export function scatter_getScatter(data) {
	return {
		type: GETSCATTER,
		data: data
	}
}
export function eos_getEOS(data) {
	return{
		type:GETEOS,
		data:data
	}
}
export function eos_setExchangeList(data) {
	return{
		type:SETEXCHANGELIST,
		data:data
	}
}
export function isPC(data) {
	return{
		type:ISPC,
		data:data
	}
}


