import React, {Component} from 'react';
import ScatterJS from '../../core/scatter.js';
import Eos from 'eosjs';
import Exchange from '../exchange/Exchange';
import {network, eosOptions} from '../../core/config.js';
import {bindActionCreators} from 'redux';
import {sortBy} from '../../util/Util.js';
import {connect} from 'react-redux';
import * as Actions from '../../redux/actions.js';
import Main from '../main/Main.js';
import Buy from '../buy/Buy.js';
import {BrowserRouter as Router, Route, Switch, Redirect,Link} from "react-router-dom";
import './App.css';
import '../../lib/css/bootstrap.min.css';
import '../../lib/css/iconfont.css';

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			active: 1
		}
	}

	componentWillMount() {
		this.props.actions.isPC({isPC: this.isPC()})
		let _this = this;
		ScatterJS.scatter.connect('eosnamedapp1').then(connected => {
			if (!connected) {
				_this.props.actions.scatter_getScatter({scatter: null});
				return false;
			}
			let scatter = ScatterJS.scatter;
			window.scatter = null;
			let eos = scatter.eos(network, Eos, eosOptions);
			_this.props.actions.scatter_getScatter({scatter: scatter});
			_this.props.actions.eos_getEOS({eos: eos})
		}).catch(e=>{
			console.log(e);
		})
		let config = {
			chainId: 'aca376f206b8fc25a6ed44dbdc66547c36c6c33e3a119ffbeaef943642f0e906', // 32 byte (64 char) hex string
			// keyProvider: ['PrivateKeys...'], // WIF string or array of keys..
			httpEndpoint: 'https://nodes.get-scatter.com:443',
			expireInSeconds: 60,
			broadcast: true,
			verbose: false, // API activity
			sign: false
		}
		let eos = Eos(config);
			eos.getTableRows({
				"scope": 'eosnamedapp1',
				"code": 'eosnamedapp1',
				"table": "accountbill",
				"json": true,
				'lower_bound': "0",
				'upper_bound': "-1",
				"limit": 1000,
			}).then(data => {
				console.log(data);
				let sortedData = data.rows.sort(sortBy('price'));
				_this.props.actions.eos_setExchangeList({namelist: sortedData});
			})
	}

	isPC = () => {
		let userAgentInfo = navigator.userAgent;
		let Agents = new Array("Android", "iPhone", "SymbianOS", "Windows Phone", "iPad", "iPod");
		let flag = true;
		for (var v = 0; v < Agents.length; v++) {
			if (userAgentInfo.indexOf(Agents[v]) > 0) {
				flag = false;
				break;
			}
		}
		return flag;
	}

	render() {
		return (
			<Router>
				<div>
					<header style={{display:this.props.isPC?'flex':'none'}} className="App">
						<header className="App-header">
							<img
								src='https://images.buddydc.com/rs/imgs/1537113498127_53dbd696b9c911e8a4ac00163e1237df.jpeg'
								className="App-logo" alt="logo"/>
							<h1 style={{margin: 0, fontSize: '2rem'}} className="App-title">EOS Name Exchange</h1>
							<div style={{marginLeft:'5rem'}} className="btn-group" role="group">
								<button type="button" className="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
									交易
									<span className="caret"></span>
								</button>
								<ul className="dropdown-menu">
									<li><Link to="/exchange">购买</Link></li>
									<li><Link to="/exchange/sale">出售</Link></li>
								</ul>
							</div>
							<button
								onClick={()=>{window.location.href=window.location.origin+'/main'}}
								style={{marginLeft:'5rem'}}
								type="button"
								className="btn btn-default dropdown-toggle"
							>
								我的
							</button>
						</header>
					</header>
					<main>
						<Switch>
							<Route path="/main" component={Main}/>
							<Route path="/exchange" component={Exchange}/>
							<Route path="/buy" component={Buy}/>
							<Redirect to="/exchange"/>
						</Switch>
					</main>
				</div>
			</Router>
		);
	}
}

function mapStateToProps(state) {
	return {
		isPC: state.isPC.isPC
	}
}

function mapDispatchToProps(dispatch) {
	return {
		actions: bindActionCreators(Actions, dispatch)
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(App)



