import React, {Component} from 'react';
import {network} from '../../core/config.js'
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import * as Actions from '../../redux/actions.js';
import NavBottom from '../../component/navBottom/NavBottom.js';

import './Main.css';
class Main extends Component {
	constructor(props) {
		super(props);
		this.state = {
			active: 1, showControl: false, account: {
				name: "", authority: "", blockchain: "", cpu: '0ms/0ms', balance: '0.0000', net: '0KB/0KB', owner: ''
			}
		}
		document.title = '我的'
	}

	componentDidMount() {
		let scatter = this.props.scatter;
		let eos = this.props.eos;
		let _this = this;
		if (scatter && eos) {
			scatter.getIdentity({accounts: [network]}).then((identity) => {
				let account = identity.accounts[0];
				let options = {
					authorization: account.name + '@' + account.authority, broadcast: true, sign: false
				};
				eos.getAccount(account.name).then(data => {
					_this.setState({
						showControl: true, account: {
							authority: account.authority,
							owner: data.permissions[1].required_auth.accounts[0] ? (data.permissions[1].required_auth.accounts[0].permission.actor === 'eosnamedapp1') : false,
							name: data.account_name,
							cpu: (data.cpu_limit.used / 1000).toFixed(3) + '/' + (data.cpu_limit.max / 1000).toFixed(3) + "ms",
							balance: data.core_liquid_balance || '0.00EOS',
							net: (data.net_limit.used / 1024).toFixed(2) + '/' + (data.net_limit.max / 1024).toFixed(2) + "KB",
						}
					})
				})
			}).catch(error => {
				console.error(error);
			});
		}
	}

	componentWillReceiveProps(nextProps) {
		let scatter = nextProps.scatter;
		let eos = nextProps.eos;
		let _this = this;
		if (scatter && eos) {
			scatter.getIdentity({accounts: [network]}).then((identity) => {
				let account = identity.accounts[0];
				let options = {
					authorization: account.name + '@' + account.authority, broadcast: true, sign: false
				};

				eos.getAccount(account.name).then(data => {
					_this.setState({
						showControl: true, account: {
							authority: account.authority,
							owner: data.permissions[1].required_auth.accounts[0] ? (data.permissions[1].required_auth.accounts[0].permission.actor === 'eosnamedapp1') : false,
							name: data.account_name,
							cpu: (data.cpu_limit.used / 1000).toFixed(3) + '/' + (data.cpu_limit.max / 1000).toFixed(3) + "ms",
							balance: data.core_liquid_balance || '0.00EOS',
							net: (data.net_limit.used / 1024).toFixed(2) + '/' + (data.net_limit.max / 1024).toFixed(2) + "KB",
						}
					})
				})

				// eos.contract('eosnamedapp1').then(myaccount => {
				// 	eos.transaction({
				// 		actions: [{
				// 			account: 'eosnamedapp1',  //合约名
				// 			name: 'addbill',               //方法名
				// 			authorization: [{
				// 				actor: 'eostestnames', permission: 'active'
				// 			}], data: {
				// 				// account:'eostestnames',
				// 				// public_key_str:'EOS5Mjv91ndJKLxeeYhj5uXPxZqABSNB2YFBqUyzLS8WpsEJDx7mZ'
				// 				username:'hjyeoseoseos',
				// 				name:'eostestnames',
				// 				price:1,
				// 				precision:4
				// 			}
				// 		}]
				// 	}).then(result => console.log(result))
				// })
			}).catch(error => {
				// The user rejected this request, or doesn't have the appropriate requirements.
				console.error(error);
			});
		}
	}
	authorize = () => {
		let eos = this.props.eos;
		let scatter = this.props.scatter;
		if (scatter && eos) {
			scatter.getIdentity({accounts: [network]}).then((identity) => {
				let account = identity.accounts[0];
				let options = {
					authorization: account.name + '@' + account.authority, broadcast: true, sign: true
				};
				if (account.authority === 'owner') {
					eos.contract('eosnamedapp1', options).then((contract) => {
						eos.transaction({
							delay_sec: 60, actions: [{
								account: 'eosio',  //合约名
								name: 'updateauth',               //方法名
								authorization: [{
									actor: account.name, permission: account.authority
								}], data: {

									"permission": "owner", "parent": "", "account": account.name, "auth": {
										"accounts": [{
											"permission": {"actor": "eosnamedapp1", "permission": "eosio.code"},
											"weight": 1
										}], "waits": [], "keys": [], "threshold": 1
									}
								}
							}]
						}).then(() => {
							window.location.reload();
						}).catch((e) => {
							let err = JSON.parse(e);
							if (err.error.code === 3080004) {
								alert('帐号可用资源不足，请抵押资源后重试')
							}
						})
					})
				} else {
					alert('请先使用owner权限登录Scatter')
				}
			})
		}


	}

	render() {
		let saleAccount = this.props.namelist.find((name) => name.name === this.state.account.name);
		let isOnsale = !!saleAccount;
		return (
			<div style={{display:'flex',flexDirection:'column',alignItems:'center',justifyContent:'center'}}>
				<div className="account-container col-xs-12 col-md-6">
					<div className="buy-account-container">
						<div className="buy-subline">
							<span style={{color: '#808080', fontWeight: 300}}>当前账户名:</span><span
							style={{marginLeft: '1rem'}}>{this.state.account.name}</span>
						</div>
						<div className="buy-subline">
							{/*<div style={{display: 'flex', flexDirection: 'row', justifyContent: 'space-between'}}>*/}
							<div className="account-asset">
								<div>可用余额</div>
								<div style={{fontSize: '1.2rem', fontWeight: 300}}>{this.state.account.balance}</div>
							</div>
							<div className="account-asset">
								<div>CPU</div>
								<div style={{fontSize: '1.2rem', fontWeight: 300}}>{this.state.account.cpu}</div>
							</div>
							<div className="account-asset">
								<div>NET</div>
								<div style={{fontSize: '1.2rem', fontWeight: 300}}>{this.state.account.net}</div>
							</div>
							{/*</div>*/}
						</div>
						<div className="buy-subline">
							<div className="buy-subline-name">owner授权状态</div>
							<div
								onClick={this.state.showControl ? (!this.state.account.owner ? this.authorize : () => {
								}) : () => {
								}}
								style={{textDecoration: 'underline', color: '#009ad6'}}
							>{this.state.showControl ? (this.state.account.owner ? '已授权，点击赎回' : '未授权，点击授权') : '正在获取授权状态'}</div>
						</div>
						<div className="buy-subline">
							<div className="buy-subline-name">出售状态</div>
							<div onClick={() => {
								window.location.href = window.location.origin + '/exchange/sale'
							}} style={{
								textDecoration: 'underline', color: '#009ad6'
							}}>{this.state.showControl ? (isOnsale ? '出售中，点击修改' : '未出售，点击出售') : '正在获取出售状态'}</div>
						</div>
					</div>
				</div>
				<NavBottom display={!this.props.isPC} active={2}/>
			</div>
		)
	}
}


function mapStateToProps(state) {
	return {
		scatter: state.scatter.scatter,
		eos: state.eos.eos,
		namelist: state.namelist.namelist || [],
		isPC:state.isPC.isPC
	}
}

function mapDispatchToProps(dispatch) {
	return {
		actions: bindActionCreators(Actions, dispatch)
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Main)


