import { combineReducers } from 'redux';
import {
	GETSCATTER,
	GETEOS,
	SETEXCHANGELIST,
	ISPC
} from './actions.js';

function getScatter(state = {}, action) {
	switch (action.type) {
		case GETSCATTER:
			return Object.assign({}, action.data);
		default:
			return state
	}
}

function getEos(state = {}, action) {
	switch (action.type) {
		case GETEOS:
			return Object.assign({}, action.data);
		default:
			return state
	}
}

function exchangeList(state = {}, action) {
	switch (action.type) {
		case SETEXCHANGELIST:
			return Object.assign({}, action.data);
		default:
			return state
	}
}
function isPC(state = {}, action) {
	switch (action.type) {
	case ISPC:
			return Object.assign({}, action.data);
	default:
		return state
	}
}

const exchangeapp = combineReducers({
	scatter:getScatter,
	eos:getEos,
	namelist:exchangeList,
	isPC : isPC
});

export default exchangeapp;



