import React, {Component} from 'react';
import {Link} from "react-router-dom";
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import * as Actions from '../../redux/actions.js';
import './ExchangeList.css'
class ExchangeList extends Component {
	constructor() {
		super();
		this.state = {
			active: 1,
			namelist:[],
			sortby:false,
			showsort:true
		}
		document.title = '出售列表'
	}
	componentDidMount(){
		this.setState({
			namelist:this.props.namelist
		})
	}
	componentWillReceiveProps(nextProps){
		if(!this.props.namelist.length){
			this.setState({
				namelist:nextProps.namelist
			})
		}
	}

	render() {
		let namelist = this.state.namelist.map((d,index)=>{
			return(
				<Link
					to={`/buy?username=${d.username}&account=${d.account}&price=${(d.price/Math.pow(10,d.precision)).toFixed(4)}`}
					// style={{width:window.innerWidth}}
					key={index}
					className="exchange-container">
					<div className="exchange-wrapper">
						<div className="exchange-ownername">
							<div>{d.account}</div>
							<div>{d.price/Math.pow(10,d.precision)+' EOS'}</div>
						</div>
						<div className="exchange-memo">{d.memo||'暂无介绍'}</div>
					</div>
				</Link>
			)
		});
		return (
		<div style={{width:window.innerWidth,display:'flex',justifyContent:'center',}}>
			<div className="col-xs-12 col-md-6"  style={{paddingBottom:'5rem'}}>
				<div className="sub-line"
				     style={{display:'flex',justifyContent:'flex-end',}}
				     onClick={()=>{this.setState({namelist:this.state.namelist.reverse(),sortby:!this.state.sortby})}}>
					<div className="sort-icon-container">
						<i style={{ fontSize: '2.4rem',display:this.state.sortby?'block':'none' }} className="iconfont">&#xe63a;</i>
						<i style={{ fontSize: '2.4rem',display:!this.state.sortby?'block':'none' }} className="iconfont">&#xe63b;</i>
					</div>
				</div>
				{namelist}
			</div>
		</div>

		);
	}
}
function mapStateToProps(state) {
	return {
		eos: state.eos.eos,
		namelist:state.namelist.namelist||[]
	}
}

function mapDispatchToProps(dispatch) {
	return {
		actions: bindActionCreators(Actions, dispatch)
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(ExchangeList)



